@import "reset.scss";
@import "_variables.scss";
@import "modules/_mixins.scss";
@import "modules/_navigation.scss";
@import "modules/_banner-imgs.scss";
@import "modules/_articles.scss";
@import "modules/_60seconds.scss";
@import "modules/_blogposts.scss";
@import "modules/_sidebar.scss";
@import "modules/_search.scss";
@import "modules/_404.scss";
@import "modules/_pages.scss";
@import "modules/_footer.scss";

* {
    &,
    &:before,
    &:after {
        -webkit-box-sizing: border-box;
           -moz-box-sizing: border-box;
                box-sizing: border-box;
        }
}


body {
	background:$background;
	font-size:16px;
	line-height:1.6rem;
	font-family:arial;
  -webkit-text-size-adjust: 100%;
}

main { display:block;
max-width:850px; }
a { color:$primary-purple; }


.flex-container{
  @include breakpoint(min-sm){
    display: -ms-flex;
    display: -webkit-flex;
    display: flex;
    flex-wrap: wrap;
     justify-content: space-between;
  }
}

header .page { position:relative; }
header .logo img { max-width: 200px; padding: 10px 0 0; }

footer {
  background: $white; padding: 1rem; font-size: 13px;
  .disclaimer {
    font-size: 11px; line-height: 18px; font-style: italic;
  }
  #site-generator {
    margin-bottom: 1rem;
  }
}

.border-v { border-left:$charcoal solid 3px; }

h1, h2 {
	font-family:'Corbel';
  color: $charcoal;
}

h1 { padding-left:1rem; margin-bottom:2rem; font-size:2rem; line-height:2.6rem; }
h2 { margin-bottom:1rem; font-size:1.6rem; line-height:2rem; }

p { margin-bottom:1rem; color:$charcoal;}

.cat4 { border-color:$cat1;}
.cat1592 { border-color:$cat2; }
.cat1593 { border-color:$cat3;}
.cat1594 { border-color:$cat4; }
.cat1595 { border-color:$cat5; }

$bgcolors: (
  white:$white
);

@each $name, $hex in $bgcolors {
  .u-bg--#{$name} {
    background: $hex;
  }
}

.list-menu {
  list-style-type: none;
  padding: 0;
  li {
    display: inline-block;
    margin-right: 1rem;
    a {color: $text;text-decoration: none;
      &:hover {
        color: $primary-purple;
        text-decoration: underline;
      }}
  }
}

#container {
  max-width:850px;
  margin:0 auto;
  @media screen and (min-width:$md) {
    display:flex;
    justify-content: space-between;
  }

}

/* visibility of access stuff hidden */
#access {
  position: absolute;
  top: -1000px;
  left: -1000px;
  height: 1px;
  width: 1px;
  text-align: left;
  overflow: hidden;
  :active,
  :focus,
  :hover {
      left: 0;
      top: 0;
      width: auto;
      height: auto;
      overflow: visible;
  }
}

#content {
  img {
  max-width:100%!important;
  height:auto!important;
  float:left;
  margin:10px;
  }
}

